import { useState } from "react";
import ModalMenu from "../../../Ui/Modal/ModalMenu";
import {
  useFeedbackQuestionRemove,
  useFeedbackQuestionUpdate,
} from "../../../Hooks/ApiHooks/InstituteFeedback/institute-feedback-api";
import { useTranslation } from "react-i18next";
import FeedbackAddQuestion from "./FeedbackAddQuestion";

const FeedbackQuestionMenu = ({ onClose, ifid, question, onRefetch }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [edit, setEdit] = useState(false);
  const [feedbackQuestionRemove] = useFeedbackQuestionRemove();
  const [feedbackQuestionUpdate] = useFeedbackQuestionUpdate();

  const onDelete = () => {
    if (question?._id && ifid) {
      setDisabled((pre) => !pre);
      feedbackQuestionRemove({
        ifid: ifid,
        questionId: question?._id,
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  const onToggle = () => {
    setEdit((pre) => !pre);
  };
  const onEditQuestion = (data) => {
    if (question?._id && ifid) {
      onToggle();
      setDisabled((pre) => !pre);
      feedbackQuestionUpdate({
        ifid: ifid,
        questionId: question?._id,
        questionCreate: { questions: data },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  return (
    <>
      {edit ? (
        <FeedbackAddQuestion
          onClose={onToggle}
          onEditAction={onEditQuestion}
          isAuthority
          question={question}
        />
      ) : (
        <ModalMenu onClose={onClose} disabled={disabled}>
          <h6 onClick={onToggle}>{t("edit")}</h6>
          <h6 onClick={onDelete}>{t("remove")}</h6>
        </ModalMenu>
      )}
    </>
  );
};

export default FeedbackQuestionMenu;
